// =============================
// variables.scss
// =============================
// Contains all the useful variables for colors, sizes...

// THEME COLORS
:root{

    --c-themePrimaryLight: #cccccc;
    --c-themePrimaryDark:  #101010;
    --c-themeSecondaryLight: #DADADA;
    --c-themeSecondaryDark: #252525;
    --c-themeTerniaryLight: #AEAEAE;
    --c-themeTerniaryDark: #515151;
    --c-themeQuaternaryLight: #919191;
    --c-themeQuaternaryDark: #888888;

    --c-themeHueRed: #C02717;
    --c-themeHueGreen: #bada06;
    --c-themeHueBlue: #2E86AB;
    --c-themeHueOrange: #E59500;
    --c-themeHuePurple: #9F00CE;
    --c-themeHueBrown: #230007;
    --c-themeHueYellow: #c4a000;
    --c-themeHueDarkGreen: #00A000;


    //post colors
    --c-postWindows: #bada06;
    --c-postApple: #799100;
    --c-postPodcast: #74918e;
    --c-postBlog: #003097;
    --c-postBits: #5fa4e9;
    --c-postRetro: #4e0b1f;
    --c-postBusiness: #b55f79;

    //menu formatting
    --c-headerImageFilter: invert(0);                    //what to do with the top image
    --c-menuLinks: var(--c-themeSecondaryLight);
    --c-menuDepth0: var(--c-themeSecondaryLight);
    --c-menuItemsFilter: none;                            //what to do with normal menu items
    --c-menuItemsFilterHover: brightness(200%);           //what to do when hovering over a menu icon or item
    --c-menuItemsScaleHover: scale(1.25);
    --c-menuShadow: rgba(255, 255, 255, 0.2);

    //article formatting
    --c-articleHeading: var(--c-themePrimaryLight);       //snippet/article title
    --c-articleParagraph: var(--c-themeSecondaryLight);
    --c-articleBackground: var(--c-themePrimaryDark);

    //snippet formatting
    --c-snippetHeading: var(--c-themePrimaryLight);       //snippet text color
    --c-snippetDate: var(--c-themeSecondaryLight);        //date
    --c-snippetAuthor: var(--c-themePrimaryLight);        //author color
    --c-snippetShadowHover: rgba(255, 255, 255, 0.5);   //shadow box around article snippet when hovering
    --c-snippetShadow:  rgba(0, 0, 0, 0);               //shadow box always around articles

    --c-postTitle: var(--c-themePrimaryLight);
    --c-postText: var(--c-themePrimaryLight);

    --c-pageTitle: var(--c-themePrimaryLight);
    --c-pageText: var(--c-themePrimaryLight);

    --c-feed-title: var(--c-themePrimaryLight);

    --c-pageBackground: var(--c-themePrimaryDark);

    --c-link: var(--c-themeTerniaryLight);
    --c-linkVisited: var(--c-themeQuaternaryLight);
    --c-linkHover: var(--c-themeHueOrange);
    --c-linkActive: var(--c-themePrimaryLight);

    --c-footerText: var(--c-themePrimaryLight);
    --c-footerImageFilter: invert(1);

    --c-codeShadow:  rgba(0, 0, 0, 0);
    --c-codeShadowHover:  rgba(0, 0, 0, 0);

    --c-navigator: var(--c-themePrimaryDark);
    --c-navigatorFilter: invert(1);

    --c-noCategoryBackground: var(--c-themePrimaryLight);
    --c-noCategoryText: var(--c-themePrimaryDark);

    --c-tableBorder: var(--c-themePrimaryLight);

    --c-subtitle: var(--c-themeTerniaryLight);

    //Syntax highlighting.
    .highlight pre { background-color: #272822; }
    .highlight .hll { background-color: #272822; }
    .highlight .c { color: #75715e } /* Comment */
    .highlight .err { color: #960050; background-color: #1e0010 } /* Error */
    .highlight .k { color: #66d9ef } /* Keyword */
    .highlight .l { color: #ae81ff } /* Literal */
    .highlight .n { color: #f8f8f2 } /* Name */
    .highlight .o { color: #f92672 } /* Operator */
    .highlight .p { color: #f8f8f2 } /* Punctuation */
    .highlight .cm { color: #75715e } /* Comment.Multiline */
    .highlight .cp { color: #75715e } /* Comment.Preproc */
    .highlight .c1 { color: #75715e } /* Comment.Single */
    .highlight .cs { color: #75715e } /* Comment.Special */
    .highlight .ge { font-style: italic } /* Generic.Emph */
    .highlight .gs { font-weight: bold } /* Generic.Strong */
    .highlight .kc { color: #66d9ef } /* Keyword.Constant */
    .highlight .kd { color: #66d9ef } /* Keyword.Declaration */
    .highlight .kn { color: #f92672 } /* Keyword.Namespace */
    .highlight .kp { color: #66d9ef } /* Keyword.Pseudo */
    .highlight .kr { color: #66d9ef } /* Keyword.Reserved */
    .highlight .kt { color: #66d9ef } /* Keyword.Type */
    .highlight .ld { color: #e6db74 } /* Literal.Date */
    .highlight .m { color: #ae81ff } /* Literal.Number */
    .highlight .s { color: #e6db74 } /* Literal.String */
    .highlight .na { color: #a6e22e } /* Name.Attribute */
    .highlight .nb { color: #f8f8f2 } /* Name.Builtin */
    .highlight .nc { color: #a6e22e } /* Name.Class */
    .highlight .no { color: #66d9ef } /* Name.Constant */
    .highlight .nd { color: #a6e22e } /* Name.Decorator */
    .highlight .ni { color: #f8f8f2 } /* Name.Entity */
    .highlight .ne { color: #a6e22e } /* Name.Exception */
    .highlight .nf { color: #a6e22e } /* Name.Function */
    .highlight .nl { color: #f8f8f2 } /* Name.Label */
    .highlight .nn { color: #f8f8f2 } /* Name.Namespace */
    .highlight .nx { color: #a6e22e } /* Name.Other */
    .highlight .py { color: #f8f8f2 } /* Name.Property */
    .highlight .nt { color: #f92672 } /* Name.Tag */
    .highlight .nv { color: #f8f8f2 } /* Name.Variable */
    .highlight .ow { color: #f92672 } /* Operator.Word */
    .highlight .w { color: #f8f8f2 } /* Text.Whitespace */
    .highlight .mf { color: #ae81ff } /* Literal.Number.Float */
    .highlight .mh { color: #ae81ff } /* Literal.Number.Hex */
    .highlight .mi { color: #ae81ff } /* Literal.Number.Integer */
    .highlight .mo { color: #ae81ff } /* Literal.Number.Oct */
    .highlight .sb { color: #e6db74 } /* Literal.String.Backtick */
    .highlight .sc { color: #e6db74 } /* Literal.String.Char */
    .highlight .sd { color: #e6db74 } /* Literal.String.Doc */
    .highlight .s2 { color: #e6db74 } /* Literal.String.Double */
    .highlight .se { color: #ae81ff } /* Literal.String.Escape */
    .highlight .sh { color: #e6db74 } /* Literal.String.Heredoc */
    .highlight .si { color: #e6db74 } /* Literal.String.Interpol */
    .highlight .sx { color: #e6db74 } /* Literal.String.Other */
    .highlight .sr { color: #e6db74 } /* Literal.String.Regex */
    .highlight .s1 { color: #e6db74 } /* Literal.String.Single */
    .highlight .ss { color: #e6db74 } /* Literal.String.Symbol */
    .highlight .bp { color: #f8f8f2 } /* Name.Builtin.Pseudo */
    .highlight .vc { color: #f8f8f2 } /* Name.Variable.Class */
    .highlight .vg { color: #f8f8f2 } /* Name.Variable.Global */
    .highlight .vi { color: #f8f8f2 } /* Name.Variable.Instance */
    .highlight .il { color: #ae81ff } /* Literal.Number.Integer.Long */

    //.highlight .gh { } /* Generic Heading & Diff Header */
    .highlight .gu { color: #75715e; } /* Generic.Subheading & Diff Unified/Comment? */
    .highlight .gd { color: #f92672; } /* Generic.Deleted & Diff Deleted */
    .highlight .gi { color: #a6e22e; } /* Generic.Inserted & Diff Inserted */
}

hyvor-talk-comments {
  --ht-color-accent: #d91c1c;
  --ht-color-accent-text: #000000;
  --ht-color-box: #ffffff;
  --ht-color-box-text: #000000;
  --ht-color-box-text-light: #0000000;
  --ht-color-box-secondary: #000000;
  --ht-color-box-secondary-text: #ffffff;
  --ht-box-shadow: 5px 5px 0px rgba(255, 255, 255, 0.05);
  --ht-box-radius: 10px;
  --ht-box-border: 1px solid transparent;
  --ht-button-radius: 10px;
}

// SIZES
:root{
    
    --s-p: 1rem;
}

// FONTS
:root{
  --f-header: Arial Bold;
  --f-text: Arial;
}

// FIXED SIZES
$s-menuItemWidth: 10rem;
$s-menuItemPadding: 1rem;
$s-m-menuMaxHeight: 1000px;

// TRANSITIONS
$t-smooth: ease 1s;